html,
body {
    margin: 0;
    padding: 0;
}

body {

    font-family: $primary_font;
}

.main-wrapper {

    max-width: 100%;
    margin: auto;
    width: 1200px;
    background:$white;
}

@media( max-width: 1100px) {

    .main-wrapper {

        width: auto;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {

    font-family: $secondary_font;
}

h4 {
    font-size: 19px;
}

h5 {
    font-size: 16px;
}

a {
    color: inherit;
    transition-duration: .6s;
}

a:hover,
a:focus,
a:active {
    text-decoration: none;
    transition-duration: .6s;
    color: rgba(244, 67, 54, 1);
}


// aligment 

.alignleft {
    
    display: inline;
    float: left;
    margin-right: 15px;
}

.alignright {

    display: inline;
    float: right;
    margin-left: 15px;
}

.aligncenter {

    clear: both;
    display: block;
    margin: 0 auto;
}


// gutenberg aligment ( full width & wide width )


body.no-sidebar .alignfull {

    width: 90vw;
    max-width: 90vw;
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}


body.no-sidebar .alignwide {

    width: 70vw;
    width: 70vw;
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

body.no-sidebar.boxed .alignfull,
body.no-sidebar.boxed .alignwide {

    width:100%;
    max-width:100%;
    margin-left: inherit;
    -webkit-transform:none;
    -moz-transform:none;
    -o-transform:none;
    -ms-transform:none;
    transform:none;
}

.site-content {
    margin-bottom: 50px;
    margin-top: 50px;
}

.section-title,
.sidebar .widget-title {

    font-family: $secondary_font;
}


.top-header {

    background-color: rgba(0, 0, 0, 1);
    padding-top: 5px;
    padding-bottom: 5px;
}

.ticker-title-container {

    background-color: rgba(244, 67, 54, 1);
    padding: 5px 0px;
}

.ticker-title {

    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
    color: rgba(255, 255, 255, 1);
    margin-top: 0px;
    margin-bottom: 0px;
}

.ticker-detail-container {

    padding: 5px 0px;
}

.ticker-news {

    margin-top: 0px;
    margin-bottom: 0px;
}

.ticker-news a {

    color: rgba(255, 255, 255, 1);
    overflow: hidden;
}

.current-date-container,
.social-menu-container {

    float: right;
    text-align: right;
}

.current-date {

    color: rgba(255, 255, 255, 1);
    margin-bottom: 0px;
    margin-top: 0px;
    margin-left: 20px;
    line-height: 24px;
}

.social-menu {

    margin-bottom: 0px;
    margin-left: 0px;
    padding-left: 0px;
}

.social-menu li {

    font-size: 0px;
    float: left;
    margin-left: 15px;
    text-align: center;
}


.social-menu li a {

    color: rgba(255, 255, 255, 1);
    display: block;
}

.social-menu li a[href*=".com"]::before {

    font-family: 'FontAwesome';
    font-size: 14px;
    color: rgba(255, 255, 255, 1);
    line-height: 24px;
}

.social-menu li a[href*="facebook.com"]::before {

    content: "\f09a";
}

.social-menu li a[href*="plus.google.com"]::before {

    content: "\f0d5";
}

.social-menu li a[href*="twitter.com"]::before,
.social-menu li a[href*="x.com"]::before {

    content: "\e61b";
}

.social-menu li a[href*="instagram.com"]::before {

    content: "\f16d";
}

.social-menu li a[href*="youtube.com"]::before {

    content: "\f16a";
}


/*
*************************************************************
*
* Spacings styles
*
*************************************************************
*/

.featured-widget-container {

    margin-bottom: 50px;
}


.middle-widget-container {
    
    margin-bottom: 50px;
}

.middle-widget-container-spacing {

    margin-bottom: 50px;
}

.news-section {
    
    margin-bottom: 40px;
}

.inner-page-container {
    
    margin-bottom: 50px;
}

.highlight-section {
    
    margin-bottom: 40px;
}

.highlight-section:last-child {
    
    margin-bottom: 0px;
}

.entry-meta span {
    
    margin-right: 15px;
}


/*
*************************************************************
*
* Mid header styles
*
*************************************************************
*/


.middle-header {

    padding-top: 30px;
    padding-bottom: 30px
}

.site-title {

    margin-top: 0px;
    margin-bottom: 0px;
    text-transform: uppercase;
    font-weight: bold;
}

.site-description {

    margin-top: 5px;
    margin-bottom: 0px;
}

.header-ad {
    text-align: right;
}


/*
*************************************************************
*
* Bottom header styles
*
*************************************************************
*/

.bottom-header {
    border-top: 1px solid rgba(0, 0, 0, .05);
    margin-bottom: 0px;
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1);
}

.navigation-menu {
    border: none;
    background-color: transparent;
}

.navbar {
    margin-bottom: 0px;
}

.navbar-collapse {
    padding-right: 0px;
    padding-left: 0px;
}

.navigation-menu .navbar-nav>li>a {
    text-transform: uppercase;
    color: rgba(0, 0, 0, 1);
    line-height: 22px;
}

.navigation-menu .navbar-nav>li>a:hover,
.navigation-menu .navbar-nav>li>a:focus {
    color: rgba(244, 67, 54, 1);
}

.navigation-menu .navbar-nav>.active>a,
.navigation-menu .navbar-nav>.active>a:hover,
.navigation-menu .navbar-nav>.active>a:focus {
    background-color: rgba(0, 0, 0, 1);
    color: rgba(255, 255, 255, 1);
}

.navbar-default .navbar-nav>.open>a,
.navbar-default .navbar-nav>.open>a:hover,
.navbar-default .navbar-nav>.open>a:focus {
    background-color: transparent;
}

.navbar-nav>li>.dropdown-menu {
    padding: 0px;
    border-radius: 0px;
}

.dropdown-menu>li>a {
    padding: 10px 20px;
    text-transform: uppercase;
}

.dropdown-menu>li>a:hover,
.dropdown-menu>li>a:focus {
    background-color: transparent;
    color: rgba(244, 67, 54, 1);
}

.dropdown-menu>.active>a,
.dropdown-menu>.active>a:hover,
.dropdown-menu>.active>a:focus {
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
}

.dropdown-menu .dropdown-menu {
    left: 100%;
    top: 0;
}

@media( min-width: 767px) {

    .dropdown:hover>.dropdown-menu {
        display: block;
    }
}

.search-container {

    position: relative;
}

.search-icon {

    font-size: 18px;
    line-height: 52px;
    display: block;
    padding: 0px 15px;
    color: rgba(255, 255, 255, 1);
    background-color: rgba(0, 0, 0, 1);
}

.search-icon:hover,
.search-icon:focus {

    cursor: pointer;
}

.search-form-main-container {

    height: 0px;
    min-height: 0px;
}

.search-form-container {

    position: relative;
    display: none;
    z-index: 2;
}

.search-form-container .search-form {

    position: absolute;
    right: 0;
    width: 350px;
    padding: 30px;
    background-color: rgba(255, 255, 255, 1);
    border-top: 2px solid rgba(244, 67, 54, 1);
    border-bottom: 2px solid rgba(244, 67, 54, 1);
    border-left: 2px solid rgba(0, 0, 0, .1);
    border-right: 2px solid rgba(0, 0, 0, .1);
}

.search-form input[type='text'],
.search-form button {
    
    height: 40px;
}


.section {

    margin-top: 30px;
}


/*
*************************************************************
*
* Highlight section styles
*
*************************************************************
*/

.highlight-left,
.highlight-right {
    position: relative;
    overflow: hidden;
}

.highlight-left img {
    width: 100%;
    transition-duration: .6s;
}

.highlight-left:hover img {
    transform: scale(1.05);
    transition-duration: .6s;
}

.highlight-info .entry-meta a {
    color: rgba(255, 255, 255, .85);
}

.highlight-left .cat-links {
    position: absolute;
    left: 20px;
    top: 20px;
    right: 20px;
}

.highlight-left .cat-links a {
    padding: 5px 10px;
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
}

.highlight-left .highlight-info {
    position: absolute;
    left: 20px;
    bottom: 30px;
    right: 20px;
    color: rgba(255, 255, 255, 1);
}

.highlight-left .highlight-info .news-title {
    
    font-size: 28px;
}

.highlight-right-top {
    margin-bottom: 9px;
}

.highlight-right img {
    transition-duration: .6s;
}

.highlight-right:hover img {
    transform: scale(1.05);
    transition-duration: .6s;
}

.highlight-right .cat-links {
    position: absolute;
    left: 20px;
    top: 20px;
    right: 20px;
}

.highlight-right .cat-links a {
    padding: 5px 10px;
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
}

.highlight-right .highlight-info {
    position: absolute;
    left: 20px;
    bottom: 20px;
    right: 20px;
    color: rgba(255, 255, 255, 1);
}

.highlight-right .highlight-info .news-title {
    
    font-size: 22px;
}




/*
*************************************************************
*
* Slider highlights
*
*************************************************************
*/


.highlight-section .no-gutter {

    padding: 0px;
}

.highlight-carousel .news-highlight-content {

    position: relative;
    height:450px;
    background-size: cover;
    background-position:center center;
}


.highlight-carousel .news-highlight-content .cat-links {

    position: absolute;
    left: 20px;
    top: 20px;
    right: 20px;
}


.highlight-carousel .news-highlight-content .highlight-info {

    position: absolute;
    left: 20px;
    bottom: 20px;
    right: 20px;
    color: rgba(255, 255, 255, 1);
}


.mask {
    position: absolute;
    background-color: rgba(0, 0, 0, .2);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition-duration: .6s;
}

.highlight-left:hover .mask,
.highlight-right:hover .mask,
.highlight-carousel .news-highlight-content:hover .mask {
    background-color: rgba(0, 0, 0, 0);
    transition-duration: .6s;
}


@media ( min-width: 991px) {

    .gutter-right {
        padding-right: 5px;
    }
    .gutter-left {
        padding-left: 5px;
    }
}

@media ( max-width: 991px) {

    .highlight-right-top,
    .highlight-right-bottom {
        margin-top: 10px;
        width: 50%;
        float: left;
    }
    .highlight-right-top {
        margin-top: 10px;
    }
}

@media ( max-width: 767px) {

    .navbar-collapse {
        padding-left: 15px;
        padding-right: 15px;
    }
    .dropdown-menu {
        padding-top: 0px;
        padding-bottom: 0px;
    }
    .navbar-nav .open .dropdown-menu>li>a,
    .navbar-nav .open .dropdown-menu .dropdown-header {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .navigation-menu .navbar-toggle {
        border: none;
    }
    .navbar-toggle .icon-bar {
        width: 30px;
    }
    .navbar-toggle .icon-bar+.icon-bar {
        margin-top: 6px;
    }
    .navigation-menu .navbar-toggle .icon-bar {
        background-color: rgba(244, 67, 54, 1);
    }
    .navigation-menu .navbar-toggle:hover,
    .navigation-menu .navbar-toggle:focus {
        background-color: transparent;
    }
    .highlight-left {
        margin-bottom: 10px;
    }
    .highlight-right img {
        width: 100%;
    }
    .highlight-right-top,
    .highlight-right-bottom {
        width: 100%;
        float: none;
    }
    .highlight-left .cat-links {
        left: 20px;
        right: 20px;
        top: 20px;
    }
    .highlight-left .highlight-info {
        left: 20px;
        right: 20px;
        bottom: 20px;
    }
}




/*
*************************************************************
*
* News section styles
*
*************************************************************
*/
.section-title {
    float: left;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    background-color: #000;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 5px 15px;
}

.big-news-title {
    
    font-size: 22px;
}

.small-news-title {
    
    font-size: 18px;
}

.underline {
    height: 5px;
    width: 50px;
    background-color: rgba(0, 0, 0, 1);
    margin-top: 10px;
    margin-bottom: 20px;
}

.middle-section .news-section:last-child {
    
    margin-bottom: 0px;
}

.news-section-info {
    margin-bottom: 30px;
    border-bottom: 3px solid;
    border-left: 5px solid rgba(244, 67, 54, 1);
}

.news-cat-link {
    float: right;
    text-transform: uppercase;
    line-height: 36px;
    font-weight: bold;
}


.news-image {
    position: relative;
}

.news-detail {
    padding: 15px 0px;
}

.news-detail .entry-meta .cat-links a {
    padding: 0px;
}

.news-detail .entry-meta .cat-links a::after {
    content: ',';
}

.news-detail .entry-meta .cat-links a:first-child::before {
    content: '\f07c';
    font-family: 'FontAwesome';
    margin-right: 5px;
}

.news-detail .entry-meta .cat-links a:last-child::after {
    content: '';
}

.news-title {
    font-weight: bold;
    margin-top: 0px;
    margin-bottom: 10px;
}

.news-title a:hover {
    color: rgba(244, 67, 54, 1);
}

.posted-on {
    margin-right: 10px;
}

.byline {
    margin-right: 10px;
}

.posted-on a .entry-date::before,
.entry-meta a .entry-date::before {
    content: '\f017';
    font-family: 'FontAwesome';
    margin-right: 5px;
}

.entry-meta .author a::before {
    content: '\f007';
    font-family: 'FontAwesome';
    margin-right: 5px;
}

.entry-meta .comments-link a::before {
    content: '\f0e5';
    font-family: 'FontAwesome';
    margin-right: 5px;
}

.cat-links a {
    margin-right: 5px;
    margin-bottom: 5px;
    display: inline-block;
    padding: 5px 10px;
    color: #fff;
}

.news-section-two .news-section-content {
    padding-bottom: 0px;
}


.big-news-content .news-image,
.small-news-content .small-thumbnail,
.news-content-top .news-image {
    overflow: hidden;
}

.big-news-content .news-image img,
.small-news-content .small-thumbnail img,
.news-content-top .news-image img {
    transition-duration: 0.6s;
}

.big-news-content:hover .news-image img,
.small-news-content:hover .small-thumbnail img,
.news-content-top:hover .news-image img {
    transform: scale(1.05);
    transition-duration: 0.6s;
}

.big-news-content:hover .news-title a,
.small-news-content:hover .news-title a,
.news-content-top:hover .news-title a {
    color: rgba(244, 67, 54, 1);
}

.small-news-content {
    padding-top: 15px;
    padding-bottom: 15px;
    border-top: 1px solid rgba(0, 0, 0, .08);
    margin-bottom: 0px;
}

.small-news-container:first-child .small-news-content {
    
    padding-top: 0px;
    border-top: none;
}

.small-news-container:last-child .small-news-content {
    
    //border-bottom: 1px solid rgba(0, 0, 0, .08);
}

.news-image .cat-links {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
}

.news-content {
    margin-top: 15px;
}

.small-news-content .small-thumbnail {
    float: left;
    position: relative;
    flex-basis: auto;
    width: 35%;
}

.small-news-content .small-thumbnail img {
    width: 100%;
}

.small-news-content .news-detail {
    float: left;
    padding-top: 0px;
    padding-right: 15px;
    padding-left: 15px;
    flex-basis: auto;
    width: 65%;
}

.news-content-top {
    margin-bottom: 15px;
}

.news-section-carousel {
    margin-bottom: 10px;
}

.news-section-carousel .owl-item img {
    height: 100%;
    width: auto;
    object-fit: cover;
    object-position: center;
}

.news-section-carousel .owl-item .news-detail {
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
}

.news-section-carousel .owl-item .news-title a {
    color: rgba(255, 255, 255, 1);
}

.news-section-carousel .owl-item .entry-meta a {
    color: rgba(255, 255, 255, .7);
}

.news-section-carousel .owl-item .entry-meta a:hover {
    color: rgba(244, 67, 54, 1);
}

.news-section-carousel .owl-item .item:hover .news-title a {
    color: rgba(244, 67, 54, 1);
}

.news-section-carousel .owl-nav {
    position: absolute;
    top: 5px;
    right: 5px;
}

.news-section-carousel .owl-nav {
    font-size: 0px;
}

.news-section-carousel .owl-nav .owl-prev,
.news-section-carousel .owl-nav .owl-next {
    width: 35px;
    height: 35px;
    background-color: rgba(255, 255, 255, .8);
    float: left;
    text-align: center;
    color: rgba(244, 67, 54, 1);
    margin-left: 1px;
}

.news-section-carousel .owl-nav .owl-prev::before {
    content: '\f104';
    font-family: 'FontAwesome';
    font-size: 24px;
}

.news-section-carousel .owl-nav .owl-next::after {
    content: '\f105';
    font-family: 'FontAwesome';
    font-size: 24px;
}

.big-news-content:hover .mask,
.small-news-content:hover .mask,
.news-content-top:hover .mask,
.news-section-carousel .owl-item .item:hover .mask {
    background-color: rgba(0, 0, 0, 0);
    transition-duration: .6s;
}

@media ( min-width: 767px) {
    .small-news-container:first-child .small-news-content {
        padding-top: 0px;
        border-top: none;
    }
    .news-content-top .news-detail {
        padding: 10px 0px;
    }
}

@media ( max-width: 767px) {
    .news-content-top:last-child {
        margin-bottom: 0px;
    }
    .news-section-carousel .owl-item img {
        width: 100%;
    }
}




/*
*************************************************************
*
* Widgets styles
*
*************************************************************
*/


