    content: '\f187';
    font-family: 'FontAwesome';
    margin-right: 10px;
    margin-bottom: 10px;
}

.widget.widget_categories ul li a::before {
    content: '\f02e';
    font-family: 'FontAwesome';
    margin-right: 10px;
}

.widget .recentcomments::before {
    content: '\f075';
    font-family: 'FontAwesome';
    margin-right: 10px;
    margin-bottom: 10px;
}

.widget.widget_pages ul li a::before {
    content: '\f105';
    font-family: 'FontAwesome';
    margin-right: 10px;
}

.widget.widget_meta ul li a::before {
