/*
*************************************************************
*
* Blog page
*
*************************************************************
*/


.archive-section-content {
    margin-bottom: 40px;
}

.archive-section-content .news-detail {
    
    padding: 0px 10px;
}

.archive-section-content:last-child {
    margin-bottom: 0px;
}

.archive-news-content .news-image {
    position: relative;
    overflow: hidden;
}

.archive-news-content .news-image img {
    transition-duration: .6s;
}

.archive-news-content .news-image .cat-links {
    position: absolute;
    left: 20px;
    top: 20px;
    right: 20px;
}

.archive-news-content .news-content .btn-more {

    color: rgba(244, 67, 54, 1); 
}

.archive-news-content:hover .news-image .mask {
    background-color: rgba(0, 0, 0, 0);
    transition-duration: .6s;
}

.archive-news-content:hover .news-image img {
    transform: scale(1.05);
    transition-duration: .6s;
}


/*
*************************************************************
*
* Breadcrumb styles
*
*************************************************************
*/

.breadcrumb {
    margin-bottom: 40px;
    background-color: #000;
    background-position: center;
    background-size: cover;
    position: relative;
    border-radius: 0px;
}

.breadcrumbs .trail-items {
    margin: 0px;
    padding: 0px;
}

.breadcrumbs li {
    list-style: none;
    float: left;
}

.breadcrumbs li::after {
    content: '\f105';
    font-family: 'FontAwesome';
    font-size: 14px;
    margin-right: 15px;
    margin-left: 15px;
    color: rgba(255, 255, 255, 1);
}

.breadcrumbs li:last-child::after {
    content: '';
    font-size: 0px;
    position: absolute;
}

.breadcrumbs li a {
    color: rgba(255, 255, 255, 1);
}

.trail-end {
    color: rgba(255, 255, 255, 1);
}


/*
*************************************************************
*
* Pagination styles
*
*************************************************************
*/

.pagination {
    margin-top: 30px;
    margin-bottom: 0px;
}

.site-main .comment-navigation, 
.site-main .posts-navigation,
.site-main .post-navigation {
    margin-top: 30px;
}

.nav-links {
    text-align: center;
}

.page-numbers {
    width: 40px;
    height: 40px;
    line-height: 40px;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(244, 67, 54, 1);
    text-decoration: none;
    float: left;
    margin-right: 5px;
    transition: .6s;
    border: 1px solid rgba(244, 67, 54, 1);
}

.next.page-numbers,
.prev.page-numbers {
    font-size: 0px;
}


.prev.page-numbers:before {
    content: '\f177';
    font-family: 'FontAwesome';
    font-size: 14px;
}

.next.page-numbers:before {
    content: '\f178';
    font-family: 'FontAwesome';
    font-size: 14px;
}

.page-numbers.current {
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
}

.page-numbers:hover,
.page-numbers:focus {
    transition: .6s;
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
}

.post-navigation .nav-links .nav-previous a,
.post-navigation .nav-links .nav-next a {
    color: rgba(244, 67, 54, 1);
    text-decoration: none;
}

.post-navigation .nav-links .nav-previous a::before {
    content: '\f177';
    font-family: 'FontAwesome';
    font-size: 14px;
    margin-right: 5px;
}

.post-navigation .nav-links .nav-next a::after {
    content: '\f178';
    font-family: 'FontAwesome';
    font-size: 14px;
    margin-left: 5px;
}

.nav-links .nav-previous {
    float: left;
    text-align: left;
}

.nav-links .nav-next {
    float: right;
    text-align: right;
}


/*
*************************************************************
*
* Scroll to top styles
*
*************************************************************
*/

.scroll-top {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 5px 15px;
    color: rgba(244, 67, 54, 1);
    border: 1px solid rgba(244, 67, 54, 1);
    font-size: 18px;
    display: none;
}

.scroll-top:hover,
.scroll-top:focus {
    background-color: rgba(244, 67, 54, 1);
    color: rgba(255, 255, 255, 1);
    transition-duration: .6s;
}



/*
*************************************************************
*
* Post page styles
*
*************************************************************
*/

.single-news-content .news-detail {
    
    padding: 0px;
}

.single-news-content .cat-links {
    margin-left: 15px;
}

.single-news-content .news-image {
    margin-bottom: 15px;
}


/*
*************************************************************
