/*
*************************************************************
*
* Main Nav styles
*
*************************************************************
*/


.mean-container .mean-bar {
    background-color: transparent;
}

.mean-container a.meanmenu-reveal span {
    background-color: #000;
    width: 27px;
    height: 2px;
    margin-top: 9px;
}

.mean-container a.meanmenu-reveal span:first-child {
    margin-top: 0px;
}

.mean-container a.meanmenu-reveal.meanclose {
    font-size: 0px !important;
    transition-duration: 0s;
}

.mean-container a.meanmenu-reveal.meanclose::before {
    
    content: url('../../images/close-btn.png');
}

.main-navigation .primary-menu li a {
    padding: 16px 20px;
    display: block;
    text-transform: uppercase;
}

.main-navigation .primary-menu li.menu-item-has-children>a::after {
    content: '\f107';
    margin-left: 5px;
    font-family: 'FontAwesome';
}

.main-navigation .primary-menu li .sub-menu li a:hover,
.main-navigation .primary-menu li .sub-menu li a:focus {
    color: rgba(244, 67, 54, 1);
}

.main-navigation .primary-menu li.current-menu-item>a,
.main-navigation .primary-menu li.current-menu-item>a:hover,
.main-navigation .primary-menu li.current-menu-item>a:focus,
.main-navigation .primary-menu li .sub-menu .current-menu-item a,
.main-navigation .primary-menu li .sub-menu .current-menu-item a:hover,
.main-navigation .primary-menu li .sub-menu .current-menu-item a:focus {
    color: #fff;
    background-color: rgba(244, 67, 54, 1);
}

.main-navigation ul ul {
    margin-top: 10px;
    opacity: 0;
    transition-duration: .3s;
    transition-timing-function: ease-out;
    z-index: 10;
}

.main-navigation ul li:hover>ul,
.main-navigation ul li:focus>ul {
    margin-top: 0px;
    opacity: 1;
    transition-duration: .3s;
    transition-timing-function: ease-in;
}

.main-navigation .primary-menu li .sub-menu li a {
    background-color: #000;
    color: #fff;
    padding: 12px 20px;
}

.main-navigation ul ul ul {
    left: 100%;
}

.main-navigation ul ul li ul {
    margin-left: 10px;
    opacity: 0;
    transition-duration: .3s;
    transition-timing-function: ease-out;
}

.main-navigation ul ul li:hover>ul,
.main-navigation ul ul li:focus>ul {
    margin-top: 0px;
    margin-left: 0px;
    opacity: 1;
    transition-duration: .3s;
    transition-timing-function: ease-in;
    z-index: 11;
}

.mean-container .mean-nav ul>li>a {
    padding: 15px 30px;
    border: none;
}

.mean-container .mean-nav ul li li a {
    border: none;
}

.mean-container .mean-nav ul li a.mean-expand {
    height: 25px;
    border: none !important;
    background-color: transparent;
}

.mean-container .mean-nav ul li a.mean-expand:hover {
    background-color: transparent;
}



